:root {
    --base-font-size: 16px;
  }
  
  .container {
    background-color: #1f1f1f;
    color: #fff;
    padding: 1.25rem;
  }
  
  .title {
    margin-bottom: 1.25rem;
    font-size: 1.5rem;
  }
  
  .news-list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    gap: 1.25rem;
    justify-content: center;
  }
  
  .news-item {
    background-color: #2d2d2d;
    border-radius: 0.5rem;
    overflow: hidden;
    cursor: pointer;
    transition: background-color 0.3s;
    width: 18.75rem;
  }
  
  .news-item:hover {
    background-color: #383838;
  }
  
  .news-title {
    padding: 0.9375rem;
    font-size: 1.2rem;
    color: lightgreen;
  }
  
  .read-more {
    display: block;
    padding: 0.625rem;
    color: #00bcd4;
    text-decoration: none;
    text-align: center;
    background-color: #333;
    transition: background-color 0.3s;
  }
  
  .read-more:hover {
    background-color: #444;
  }
  
  .headline-v {
    color: red;
    font-size: 2rem;
  }

  .container.loading {
    background-color: #1f1f1f;
  }