body, html {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  background-color: #1f1f1f;
  font-family: 'YourPreferredFont', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


#root {
  height: 100%;
  display: flex;
  flex-direction: column;
}
